*body{
    box-sizing: border-box;
    margin: 0px;
    padding: 0px;
}

h1{
    color: rgb(0, 86, 247);
}
.entire{
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: linear-gradient(to right, rgba(3, 244, 224, 0.778),rgb(208, 7, 7));
    box-sizing: border-box;
}
label{
    color: red;
}
input[type='text']{
    width: 50vw;
    padding: 20px;
    margin: 5px;
}
input[type='number']{
    width: 50vw;
    padding: 20px;
    margin: 5px;
}
input[type='email']{
    width: 50vw;
    padding: 20px;
    margin: 5px;
}
input[type='password']{
    width: 50vw;
    padding: 20px;
    margin: 5px;
}
button{
    cursor: pointer;
    width: 100px;
    height: 40px;
    background: linear-gradient(rgb(255, 72, 0),rgb(0, 0, 0));
    color: white;
    font-weight: bolder;
    font-family:'Times New Roman', Times, serif;
}
.checkbox{
    display: flex;
    margin: 10px;
    justify-content: center;
    align-items: center;
}
span{
    color: rgb(0, 86, 247);
    font-weight: bolder;
}
